@import '__title/section-ig__title.scss';
.ig__item {
  position: relative;
  overflow: hidden;
  margin-bottom: 30px;
  height: 314px;
  vertical-align: middle;
  &:hover {
    .ig__overlay {
      opacity: 1;
      -webkit-transition: opacity .3s ease-in-out;
      -o-transition: opacity .3s ease-in-out;
      transition: opacity .3s ease-in-out;
    }
  }
}
.ig__overlay {
  opacity: 0;
  position: absolute;
  left: 12px;
  top: 0;
  width: calc(100% - 24px);
  height: 100%;
  background-color: rgba(0,0,0,.5);
  text-align: center;
  line-height: 314px;
  &_wrap {
    display: inline-block;
    height: auto;
    line-height: 35px;
  }
  .icon {
    fill: $white;
    font-size: 34px;
    vertical-align: middle;
  }
  &__amount {
    font-size: 25px;
    vertical-align: middle;
    font-family: sfB;
    color: $white;
  }
}