// @include font-face('sfM', '../fonts/SFUIText/SFUIText-Medium', true, 'sans-serif');
// @include font-face('sfL', '../fonts/SFUIText/SFUIText-Light', true, 'sans-serif');
// @include font-face('Charter', '../fonts/Charter/Charter', true, 'serif');
// @include font-face('HelveticaNeue', '../fonts/HelveticaNeue/HelveticaNeue', true, 'sans-serif');
// @include font-face('TimesNewRoman', '../fonts/TimesNewRoman/TimesNewRoman', true, 'sans-serif');
@include font-face(sf, "../fonts/SFUIText/SFUIText-Regular", normal, normal, eot otf svg ttf woff);
@include font-face(sfB, "../fonts/SFUIText/SFUIText-Bold", normal, normal, eot otf svg ttf woff);
@include font-face(sfM, "../fonts/SFUIText/SFUIText-Medium", normal, normal, eot otf svg ttf woff);
@include font-face(sfL, "../fonts/SFUIText/SFUIText-Light", normal, normal, eot otf svg ttf woff);
@include font-face(Charter, "../fonts/Charter/Charter", normal, normal, eot svg woff ttf);
@include font-face(HelveticaNeue, "../fonts/HelveticaNeue/HelveticaNeue", normal, normal, eot svg woff ttf);
@include font-face(TimesNewRoman, "../fonts/TimesNewRoman/TimesNewRoman", normal, normal, eot svg woff ttf);



$white: #fff;
$colorYellow: #f8eb33;
$colorTomat: #e17474;
$colorBlack: #303030;
$colorDark: #3a3a3a;
$colorGrey: #e6e6e6;
$colorMiddleGrey: #b7b7b7;
$colorDarkGrey: #949a96;
$colorPink: #e395c0;
$colorPinkLight: #f49ac1;
$colorPinkHover: #dc7ab0;
$colorPinkDeep: #ff92ce;
$colorPaleGreen: #c1d0c7;
$colorLightGreen: #e8f1ed;
$colorGreen: #698e7d;
$colorDarkGreen: #006134;
$lightPurple: #d4d3ff;
$colorOrange: #fbad7d;
$colorRed: #dc474c;
$booletColor: #7ab799;
$ratingSelect: #f5b319;
$footerShadow: #b8cabf;
* {
  box-sizing: border-box;
}
html, body {
  margin: 0;
  padding: 0;
}
body {
  background-color: #ffffff;
  font-family: "sf";
  font-size: 14px;
  line-height: 1.15;
  color: #3a3a3a;
}
article,aside,figcaption,figure,footer,header,nav,section {
  display: block;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
.icon_grey {
  fill: $colorDarkGrey;
}
.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}
.img {
  display: block;
  &-center{
    margin: 0 auto;
  }
}
.justify {
  text-align: justify;
  text-justify: newspaper;
  text-align-last: justify;
}
.justify-reset {
  text-align: initial;
  text-justify: initial;
  text-align-last: initial;
}
button:focus,
input:focus {
  -webkit-outline: none;
  -moz-outline: none;
  -ms-outline: none;
  -o-outline: none;
  outline: none;
}
.uppercase {
  text-transform: uppercase !important;
}
.lowercase {
  text-transform: none !important;
}
.border-right {
  border-right: solid 1px $colorGrey;
}
h1, .h1 {
  font-family: Charter;
  font-size: 36px;
  font-weight: normal;
  text-transform: uppercase;
}
h2, .h2 {
  font-family: Charter;
  font-size: 36px;
  font-weight: normal;
}
h3, .h3 {
  font-family: Charter;
  font-size: 30px;
  font-weight: normal;
}
h4, .h4 {
  font-family: "sfB"; 
  font-size: 18px;
}
p {
  font-size: 15px;
  margin: 0 0 10px;
}
.subtitle {
  font-family: 'Times New Roman';
  font-size: 24px;
  font-style: italic;
  padding: 8px 0 55px;
  text-align: center;
}
.italic {
  font-family: 'Times New Roman';
  font-style: italic;
  font-size: 18px;
}
.pink {
  color: $colorPinkDeep;
}
.title-light_s {
  font-family: sfl;
  font-size: 15px;
  color: $colorDark;
}
.container {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  width: 1024px;
}
.hidden {
  display: none;
}
.visible {
  display: block;
}
.pull-left {
  float: left !important;
}
.pull-right {
  float: right !important;
}
.text-left {
  text-align: left;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.text-jst {
  text-align: justify;
}
.in-bl {
  display: inline-block;
  vertical-align: middle;
}
.prel {
  position: relative;
}
.clearfix:before, .clearfix:after,
.row:before, .row:after {
  clear: both;
  content: '';
  display: table;
}
.row {
  margin: 0 -12px;
}
.col-li-4 {
  -moz-column-count: 4;
  -webkit-column-count: 4;
  column-count: 4;
  padding-top: 21px;
  @include legacy-ie(9) {
    width: 600px;
  }
}
.col {
  float: left;
  padding: 0 12px;
}
.col-1 {
  width: 8.33333333%;
}
.col-2 {
  width: 16.66666667%;
}
.col-3 {
  width: 25%;
}
.col-4 {
  width: 33.33333333%;
}
.col-5 {
  width: 41.66666667%;
}
.col-6 {
  width: 50%;
}
.col-7 {
  width: 58.33333333%;
}
.col-8 {
  width: 66.66666667%;
}
.col-9 {
  width: 75%;
}
.col-10 {
  width: 83.33333333%;
}
.col-11 {
  width: 83.33333333%;
}
.col-12 {
  width: 100%;
}

.btn {
  border-radius: 5px;
  border: 1px solid $colorPink;
  color: $colorPink;
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  line-height: 30px;
  min-width: 150px;
  padding: 5px 20px;
  &:hover {
    border-color: $colorPinkHover;
    color: $colorPinkHover;
  }
}
.btn-bg {
  background-color: $colorPink;
  color: $white;
  font-size: 18px;
  &:hover {
    background-color: $colorPinkHover;
    color: $white;
  }
}
.btn_main-banner {
  border-radius: 10px 10px 0 0;
  bottom: 0;
  box-shadow: 0 -2px 5px 0 rgba(0,0,0,.11);
  font-family: "sfM"; 
  font-size: 13px;
  padding-left: 15px;
  padding-right: 15px;
  position: absolute;
  right: 15px;
  text-transform: uppercase;
}

.link {
  cursor: pointer;
  text-decoration: none;
}
.link_white {
  color: $white;
}
.link_underline {
  text-decoration: underline;
}

.link_white.link_underline {
  text-decoration: none;
  border-bottom: 1px solid $white;
}

.link_size_s {
  font-family: "sfL"; 
  font-size: 12px;
}
.link_size_m {
  font-family: "sfL"; 
  font-size: 14px;
}
.link_more {
  border-bottom: 1px dashed;
  color: $colorPinkDeep;
}
.line {
  background: url(../images/line.png) center no-repeat;
  height: 1px;
  margin: 60px 0 85px;
}

.radio {
  cursor: pointer;
  &:before,
  &:after {
    content: '';
    display: inline-block;
  }
  &:before {
    opacity: 0;
    position: relative;
  }
  &_default {
    display: inline-block;
    color: $colorMiddleGrey;
    position: relative;
    padding-left: 32px;
    margin: 0 8px;
    &:after {
      left: 0;
      top: -2px;
      position: absolute;
      width: 21px;
      height: 21px;
      border-radius: 50%;
      border: solid 1px $colorGrey;
    }
  }
  &_color {
    margin: 0 8px;
    border-radius: 50%;
    display: block;
    width: 23px;
    height: 23px;
    position: relative;
    border-width: 3px;
    &:before {
      position: absolute;
      left: -3px;
      top: -3px;
      width: 23px;
      height: 23px;
      border-radius: 50%;
    }
  }
  &_nav {
     padding-left: 15px;
    &:hover {
      color: $colorPink;
    }
    &:before {
      left: -15px;
      @include sprite('check');
      font-size: 17px;
    }
  }
}

input[type="radio"]:checked+.radio {
  &:before {
    opacity: 1;
  }
  &_color{
    border-color: transparent;
    &:before {
      border: solid $colorDarkGreen;
    }
  }
  &_nav {
    &:hover {
      color: $colorDarkGreen;
    }
  }
  &_default {
    &:after {
      border-color: $colorDarkGreen;
    }
    &:before {
      position: absolute;
      left: 7px;
      top: 5px;
      width: 9px;
      height: 9px;
      border-radius: 50%;
      background-color: $colorDarkGreen;
    }
  }
  // &_color {
  //   &:before {
  //     display: block;
  //   }
  // }
}
.radio_color_white {
  background-color: $white;
  border: solid 1px $colorGrey;
}
.radio_color_orange {
  background-color: $colorOrange;
}
.radio_color_red {
  background-color: $colorRed;
}
.radio_color_pink {
  background-color: $colorPink;
}
