@import '__item/navbar-nav__item.scss';
@import '__link/navbar-nav__link.scss';

.navbar-nav {
  font-size: 1px;
  line-height: 0;
  margin: 0 -22px;
  position: relative;
  text-transform: uppercase;
  z-index: 11;
  zoom: 1;
  top: -2px;
  &:after {
    content: '';
    display: inline-block;
    height: 0px;
    overflow: hidden;
    visibility: hidden;
    width: 100%;
  }
  &__dropdown {
    display: block;
    opacity: 0;
    padding: 28px 10px 15px;
    top: 56px;
    transform: translateY(0);
    transition: all 0.3s ease-in-out 0s, visibility 0s linear 0.3s, z-index 0s linear 0.01s;
    visibility: hidden;
    width: 998px;
    z-index: -1;
    &:before {
      left: 30px;
    }
    &:hover {
      opacity: 1;
      transform: translateY(0%);
      transition-delay: 0s, 0s, 0.3s;
      visibility: visible;
      z-index: 12;
    }
    &_left {
      left: 25px;
    }
    &__wrap {
      margin-bottom: 37px;
    }
  }
  &__price {
    position: absolute;
    right: 41px;
    bottom: 55px;
    &_old {
      font-size: 24px;
      display: block;
    }
    &_new {
      font-size: 28px;
      display: block;
    }
  }
}