.dropdown__content {
  display: none;
  position: absolute;
  background-color: $white;
  border-radius: 5px;
  border: solid 1px $colorGrey;
  z-index: 20;
  top: 32px;
  padding: 10px 15px 15px;
  @import "dropdown__content__tel.scss";
  &:before {
    content: '';
    position: absolute;
    top: -7px;
    width: 14px;
    height: 14px;
    background-color: $white;
    border-top: solid 1px $colorGrey;
    border-left: solid 1px $colorGrey;
    transform: rotate(45deg);
  }
}