.search-form__input {
  border: none;
  box-shadow: none;
  font-family: "sfL";
  font-size: 14px;
  color: $colorDarkGreen;
  width: 90%;
  padding-left: 13px;
  vertical-align: top;

	&::-webkit-input-placeholder { /* Chrome/Opera/Safari */
		color: $colorDarkGreen;
	}
	&::-moz-placeholder { /* Firefox 19+ */
		color: $colorDarkGreen;
	}
	&:-ms-input-placeholder { /* IE 10+ */
		color: $colorDarkGreen;
	}
	&:-moz-placeholder { /* Firefox 18- */
		color: $colorDarkGreen;
	}
}