.dropdown__content__item {
  font-family: sf;
  font-size: 14px;
  line-height: 33px;
  white-space: nowrap;
  cursor: pointer;
  text-transform: none;
  color: #006134;
  &_black {
    color: $colorBlack;
  }
  &:hover {
    color: #e395c0;
  }
  &_selected {
    color: $colorPink;
  }
}

