@import '__content/fast-buy__content.scss';
@import '__range/fast-buy__range.scss';
@import '__preview/fast-buy__preview.scss';
@import '__dropdown/fast-buy__dropdown.scss';

.fast-buy {
  bottom: 0;
  color: $colorBlack;
  overflow: hidden;
  position: absolute;
  right: 15px;
  width: 319px;
  &__content {
    background-color: $white;
    border-radius: 5px 5px 0 0;
    border: solid 1px $colorGrey;
    padding: 16px 30px 16px 30px;
    position: relative;
    top: 522px;
    height: 522px;
    z-index: 11;
  }
}
.btn_main-banner {
  z-index: 10;
}
.fast-buy_close {
  content: '';
  cursor: pointer;
  display: block;
  display: block;
  float: right;
  font-size: 15px;
  margin-right: -15px;
}
.fast-buy__img {
  display: block;
  height: 150px;
  margin: 0 auto;
  width: auto;
}
.fast-buy__radio {
  margin-top: 18px;
}
.fast-buy__radio__list {
  width: 100%;
  margin: 13px -8px;
}
.fast-buy__btn {
  margin-top: 14px;
  width: 100%;
}