@import '__item/catalog__item.scss';
@import '__item/catalog__item-name.scss';
@import '__item/catalog__item-price.scss';
@import '__img/catalog__img.scss';
@import '__btn/catalog__btn-place.scss';

.catalog-pref-link {
	position: relative;
	display: inline-block;
	color: $colorDarkGrey;
	margin-top: 10px;

	&:hover {
		color: $colorDarkGreen;
	}

	&.catalog-pref-link_Bouqet {
		padding-left: 38px;
	}
	&.catalog-pref-link_Car {
		padding-left: 38px;
	}
	&.catalog-pref-link_Znak {
		padding-left: 32px;
	}

	.icon {
		position: absolute;
		left: 0;
		top: 50%;
		&.icon-Bouqet {
			font-size: 22px;
			top: -5px;
		}
		&.icon-Car {
			font-size: 18px;
			top: -3px;
		}
		&.icon-Znak {
			font-size: 25px;
			top: -6px;
					
		}
	}
}