@import '__input/search-form__input.scss';
@import '__nav/search-form__nav.scss';
@import '__close/search-form__close.scss';
.search-form {
  left: 0;
  overflow: hidden;
  position: absolute;
  top: 89px;
  width: 100%;
  &__link {
    // top: -15px;
    bottom: 12px;
    right: 15px;
    position: absolute;

  }
  &__content {
    z-index: 10;
    background-color: $white;
    left: 0;
    position: relative;
    right: 0;
    top: -69px;
    height: 69px;
    z-index: 12;
  }
}
.search-form__link__icon {
  fill: $white;
  font-size: 13px;
  margin-right: 3px;
  vertical-align: middle;
}

.search-form__wrap {
  vertical-align: top;
  padding: 9px 0 5px;
}
.search-form__wrap__icon {
  margin-left: 5px;
  margin-top: 2px;
  font-size: 14px;
  fill: $colorDarkGreen;
}