.navbar-nav__item {
  display: inline-block;
  font-size: 13px;
  line-height: normal;
  position: relative;
  vertical-align: top;
  &_title {
    font-family: sfM;
    margin-bottom: 7px;
    margin-top: 2px;
    text-transform: uppercase;
    white-space: normal;
  }
  &__link {
    font-family: sf;
    font-size: 14px;
    line-height: 33px;
    white-space: nowrap;
    cursor: pointer;
    text-transform: none;
    color: $colorDarkGreen;
  }
}