.header__cart {
  display: block;
  color: #fff;
  margin: 9px 0 7px;
  position: relative;
  .link {
    position: relative;
  }
}
.header__cart-val {
  border-radius: 50%;
  width: 14px;
  height: 14px;
  background-color: $colorPinkLight;
  font-family: "sfL";
  font-size: 10px;
  line-height: 14px;
  text-align: center;
  position: absolute;
  left: -21px;
  top: -8px;
}
.header__cart__icon {
  fill: $white;
  position: absolute;
  left: -41px;
  top: -5px;
  font-size: 21px;
}