.scroll-top {
  cursor: pointer;
  display: none;
  position: fixed;
  right: 100px;
  bottom: 100px;
  z-index: 200;
  &__wrap {
    border-radius: 5px;
    padding: 13px 8px 11px;
    vertical-align: middle;
    position: relative;
    z-index: 2;
    background-color: $colorPink;
  }
  .icon-arrowUp {
    font-size: 12px;
    fill: $white;
  }
  &__text {
    opacity: 0;
    visibility: hidden;
    left: 0;
    padding: 12px;
    width: 0;
    -webkit-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    position: absolute;
    background-color: $colorPink;
    top: 0;
    color: $white;
    font-family: sfM;
    z-index: -1;
    border-radius: 0 5px 5px 0;
  }
  &:hover {
    .scroll-top__text {
      visibility: visible;
      opacity: 1;
      left: 90%;
      width: auto;
    }
  }
}