.slider {
  height: 577px;
  left: 0;
  list-style-type: none;
  margin-top: -57px;
  overflow: hidden;
  padding: 0px;
  position: relative;
  top: 0;
  width: 100%;
  z-index: 1;
  &__item {
    cursor:         grab;
    cursor:    -moz-grab;
    cursor: -webkit-grab;
    -moz-transition:    opacity .6s ease-in-out;
    -o-transition:      opacity .6s ease-in-out;
    -webkit-transition: opacity .6s ease-in-out;
    transition:         opacity .6s ease-in-out;
    height: 100%;
    left: 0px;
    opacity: 0;
    position: absolute;
    top: 0px;
    width: 100%;
    z-index: 1;
    &:active {
      cursor:         grabbing;
      cursor:    -moz-grabbing;
      cursor: -webkit-grabbing;
    }
    &_bg {
      background-image: url(../images/sliderBg.jpg);
      height: 577px;
      width: 100%;
      .slider__item__img {
        bottom: 0;
        height: 100%;
        max-height: 515px;
        min-height: auto;
        top: auto;
      }
    }
    &_show {
      opacity: 1;
      z-index: 2;
    }
    &__img {
      display: block;
      width: auto;
      min-height: 100%;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: 0;
    }
    &__txt {
      left: 0;
      z-index: 3;
      position: absolute;
      top: 72px;
      width: 100%;
    }
  }
}
.bullet-wrap {
  position: relative;
}

.bullet {
  bottom: 30px;
  left: 0;
  margin: 0 -10px;
  position: absolute;
  width: 100%;
  z-index: 3;
  &__item {
    background-color: $white;
    border-radius: 50%;
    cursor: pointer;
    height: 14px;
    width: 14px;
    margin: 0 10px;
    &_active {
      background-color: $booletColor;
    }
  }
}
.slide-shift {
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease;
}