.modal {
  // background: #555;
  background-color: #fff;
  border-radius: 5px;
  display: none;
  height: auto;
  left: 50%;
  position: fixed;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 550px;
  z-index: 1001;
  font-family: sf;
  font-size: 16px;
  &__close {
    border: block;
    background-color: transparent;
    cursor: pointer;
    margin: 25px 22px 0 0;
    font-size: 16px;
  }
  &__content {
    padding: 0 106px;
    margin-top: 37px;
  }
  &-backdrop {
    position: fixed;
    left: 0;
    top: 0;
    opacity: 0;
    background: rgba(19,29,23,.64);
    width: 100%;
    height: 100%;
    z-index: 1000;
    -webkit-transition: opacity .5s ease;
    -o-transition: opacity .5s ease;
    transition: opacity .5s ease;
  }
}