.fast-buy__preview {
  position: absolute;
  right: calc(100% - 31px);
  width: 740px;
  height: calc(100% + 2px);
  background-color: $white;
  top: -1px;
  border-radius: 5px 0 0 0;
  border-top: solid 1px $colorGrey;
  border-left: solid 1px $colorGrey;
  border-bottom: solid 1px $colorGrey;
  visibility: hidden;
  opacity: 0;
  transform: translateX(319px);
  z-index: -1;
  transition: all .3s ease-in-out 0s, visibility 0s linear .3s, z-index .5s linear .05s;
  &:hover {
    visibility: visible;
    opacity: 1;
    z-index: -1;
    transform: translateY(0%);
    transition-delay: .2s, 0s, .5s;
  }
  &_hover {
    position: relative;
    z-index: 12;
    &:hover ~ .fast-buy__preview {
      visibility: visible;
      opacity: 1;
      z-index: -1;
      transform: translateY(0%);
      transition-delay: .2s, 0s, .5s;
    }
  }
}