.tabs__nav {
  margin: 0 -18px 20px;
  &_item {
    margin: 0 18px;
    font-size: 13px;
    color: $colorDarkGreen;
    display: inline-block;
    font-family: sf;
    text-transform: uppercase;
    cursor: pointer;
    line-height: 27px;
    vertical-align: middle;
    &_active {
      color: $colorPink;
    }
  }
}