.navbar-nav__link {
  color: $colorDarkGreen;
  padding: 21px 22px 19px;
  display: inline-block;
  .icon-arrow {
    font-size: 6px;
    vertical-align: middle;
  }
  &:hover{
    + .navbar-nav__dropdown {
      visibility: visible;
      opacity: 1;
      z-index: 1;
      transform: translateY(0%);
      transition-delay: 0s, 0s, 0.3s;
    }
  }
}
.navbar-nav__link_active {
  color: $colorPinkLight;
}