@import '__block/footer__block.scss';
@import '__bottom/footer__bottom.scss';
@import '__mail/footer__mail.scss';
@import '__title/footer__title.scss';
@import '__tel/footer__tel.scss';

.footer {
  background-color: $colorLightGreen;
  padding: 30px 0;
  margin-top: 60px;
  &__available {
    margin-left: -12px;
    .icon {
      font-size: 33px;
      fill: $footerShadow;
    }
  }
  &__social {
    margin-right: -24px;
    li {
      margin: 0 4px 0 9px;
      display: inline-block;
    }
    a {
      background-color: $footerShadow;
      border-radius: 5px;
      width: 33px;
      height: 33px;
      text-align: center;
      display: inline-block;
      line-height: 33px;
    }
    .icon {
      fill: $white;
      vertical-align: middle;
      &-Facebook {
        font-size: 21px;
      }
      &-vk {
        font-size: 14px;
      }
      &-Instagram {
        font-size: 21px;
      }
      &-Odnoclass {
        font-size: 20px;
      }
      &-twitter {
        font-size: 18px;
      }
    }
  }
}
.footer-info {
  border-bottom: 1px solid $colorPaleGreen;
  padding-bottom: 20px;
  margin-bottom: 5px;
  &__right {
    // width: 232px;
  }
}
.footer_link {
  color: $colorGreen;
  margin-bottom: 5px;
  display: inline-block;
}